.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid whitesmoke;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__left > img {
  object-fit: contain;
  height: 80px;
  margin-left: 5px;
}
.header__middle {
  display: flex;
  align-items: center;
  flex: 0.7; /* 70% of the screen */
  background-color: whitesmoke;
  padding: 10px;
  border-radius: 5px;
}
/* Toggle the material UI icon */
.header__middle > .MuiSvgIcon-root {
  color: grey;
}
.header__middle > input {
  border: none;
  width: 100%;
  padding: 10px;
  /* outline-width: 0;  or */
  outline: none;
  background-color: transparent;
}
.header__right {
  display: flex;
  /* align-items: center; */
  padding-right: 20px;
}
